import React, { Component } from 'react'
import { Link } from 'react-router-dom';
import { IoIosArrowBack } from 'react-icons/io'
import {Alert, Table} from 'reactstrap';

export default class RegisterCheck extends Component {
  state = {
    queue_number: '',
    queue: {},
    not_found : false,
    loading : false
  }

  componentDidMount = () => {
    this.setState({ queue_number: localStorage.getItem('queue_number') || '' })
  };

  check = () => {
    const { models: { Queue } } = this.props;
    const { queue_number } = this.state;
    this.setState({ loading : true, not_found : false });
    Queue.collection({ limit: 1, offset: 0, where: { queue_number }, include : [{ model : 'Purpose', attributes : ['id', 'name'] }] }).then(q => {
      if (q.rows.length > 0) {
        this.setState({ queue : q.rows[0], loading : false });
      } else {
        this.setState({ not_found : true, loading : false });
      }
    });
  }

  render() {
    const {queue} = this.state;
    return (
      <>
        <div className="bg-half" />
        <div className="wrapper d-flex justify-content-center align-items-center">
          <div className="container my-5 py-5">
            <div className="card border-0 shadow">
              <div className="card-body">
                <div className="d-flex flex-row justify-content-between align-items-center">
                  <Link to="/" className="btn btn-sm btn-outline-dark"><IoIosArrowBack /></Link>
                  <h5 className="m-0 d-block">Cek Antrian</h5>
                </div>
                <hr />
                <div className="input-group mb-3">
                  <input onChange={ev => this.setState({ queue_number: ev.target.value })} value={this.state.queue_number} type="text" placeholder="Nomor Antrian" className="form-control" />
                  <div className="input-group-append">
                    <button onClick={this.check} className="btn btn-outline-primary">Cek</button>
                  </div>
                </div>
                <Alert isOpen={this.state.loading} color="info">Mencari data</Alert>
                <Alert isOpen={this.state.not_found} color="danger">Data tidak ditemukan</Alert>
                {/* <button className="btn btn-outline-primary btn-lg btn-block my-3">Cek</button> */}
              </div>
              {Object.keys(queue).length > 0 && <Table striped>
                <tbody>
                  <tr>
                    <td className="font-weight-bold">Nama</td>
                    <td>:</td>
                    <td>{queue.name}</td>
                  </tr>
                  <tr>
                    <td className="font-weight-bold">NIK</td>
                    <td>:</td>
                    <td>{queue.nik}</td>
                  </tr>
                  <tr>
                    <td className="font-weight-bold">Tanggal</td>
                    <td>:</td>
                    <td>{queue.date}</td>
                  </tr>
                  <tr>
                    <td className="font-weight-bold">Jam</td>
                    <td>:</td>
                    <td>{queue.time}</td>
                  </tr>
                  <tr>
                    <td className="font-weight-bold">Tujuan</td>
                    <td>:</td>
                    <td>{queue.purpose.name}</td>
                  </tr>
                  <tr>
                    <td className="font-weight-bold">Nomor Antrian</td>
                    <td>:</td>
                    <td>{queue.queue_number}</td>
                  </tr>
                </tbody>
              </Table>}
            </div>
          </div>
        </div>
      </>
    )
  }
}
